          duties: '宜宾职业技术学院党委常委、副院长',
          level: '区人大代表',
          phoneNumber: '13080392515',
          headImg: require('@/assets/img/zhutao.jpg')

        },
        {
          name: '杨永康',
          duties: '翠屏区西郊街道金沙社区党委书记、居委会主任',
          level: '区人大代表',
          phoneNumber: '13684175751',
          headImg: require('@/assets/img/yangyongkang.jpg')

        },
        {
          name: '欧贵英',
          duties: '翠屏区西郊街道东福社区党委书记、居委会主任',
          level: '区人大代表',
