<style scoped>
* {
  margin: 0 auto;
  padding: 0;
  font-family: Microsoft YaHei, Helvitica, Verdana, Tohoma, Arial, san-serif, simsun;
  font-size: 14px;
  background: #ffffff;
}

div {
  display: block;
}

.main_page {
  /* border: 1px solid #83d7c8; */
  float: left;
  width: 100%;
  box-sizing: border-box;
  padding: 1%;
  margin: 20px 0;
  /* background: url(../assets/img/beijing.jpg); */
  background-repeat: repeat-x;
}

ul,
li,
p,
h1 {
  margin: 0px;
  padding: 0px;
  outline: none;
}

h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: 600;
  padding: 10px 0;
  text-align: center;
  line-height: 1.2em;
}

p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  margin: 10px;
  background: none;
}

.our_list ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
}

.our_list ul li {
