  background: #f4f4f4;
  border: 1px solid #e4e4e4;
  margin-top: 20px;
  text-align: left;
  display: inline-block;
  margin: 20px 7px 7px 7px;
  padding: 16px;
  overflow: hidden;
  cursor: pointer;
}

.our_list ul li img {
  width: 92px;
  height: 131px;
}

@media screen and (min-width: 653px) and (max-width: 1050px) {
  .our_list ul li {
    width: 200px;
    height: 341px;

  }

  .our_list ul {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 385px) and (max-width: 650px) {
  .our_list ul li {
    width: 152px;
    height: 404px;
  }

  .our_list ul {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 430px) {
  .our_list ul li {
    width: 152px;
    height: 383px;

  }

  .our_list ul {
    grid-template-columns: 1fr 1fr;
  }
}


@media screen and (max-width: 320px) {
  .our_list ul li {
    width: 119px;
    height: 500px;
  }

  .our_list ul {
    grid-template-columns: 1fr 1fr;
  }
}
</style>




